<template>
  <div class="count-box">
    <button class="minus" @click="handleSub"> - </button>
    <input type="text" class="inp" :value="value" @change="handleChange">
    <button class="add" @click="handleAdd"> + </button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      typeof: Number,
      default: 1
    }
  },
  methods: {
    handleSub () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    handleAdd () {
      this.$emit('input', this.value + 1)
    },
    handleChange (e) {
      // 转数字处理：数字/NaN
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        // 输入了不合法的文本，回退成原来的value
        e.target.value = this.value
        return
      }
      this.$emit('input', num)
    }

  }
}
</script>

<style lang="less" scoped>
.count-box{
  width: 110px;
  display: flex;
  .add, .minus{
    // display: block;
    width: 30px;
    height: 30px;
    border: 0;
    outline: none;
    background-color: #efefef;
  }
  .inp{
    width: 40px;
    height: 30px;
    outline: none;
    border: none;
    margin: 0 5px;
    background-color: #efefef;
    text-align: center;

  }
}
</style>
